.form {
    font-size: 0.75rem;
    input {
        font-size: 0.9rem;
         font-weight: 500;
        outline: none !important;
        color: #fff;
        &::-webkit-input-placeholder {
            opacity: 1;
        }
        &::-moz-placeholder {
            opacity: 1;
        }
        &:-ms-input-placeholder {
            opacity: 1;
        }
        &:-moz-placeholder {
            opacity: 1;
        }
    }
    .form-control[readonly] {
        pointer-events: none;
        background: none;
        color: rgba(255,255,255,1)
    }
    .field-validation-error {
        color: $error-color;
        font-size: 12px;
    }
    .input-validation-error {
        border: 1px solid $error-color !important;
    }
    .form-group-label {
        color: $theme-color2;
        /* font-weight: 600; */
        letter-spacing: 0.9px;
        margin-bottom: 3px;
        display: block;
        font-size:13px;
        .important {
        color: red;
        }
    }
    .form-control {
        border: none;
        background-color: transparent;
        padding: 0;
        height: 25px;
        font-size: 0.85rem;
        border-bottom: 1px solid $form-border-color;
        @include border-radius(0);
        @include transition(350ms);
        @include shadow(none !important);
      /*   &:hover {
            border-color: $theme-color2;
        } */
        &:focus {
            opacity: 0.8;
            color: #fff;
        }
        &.custom-select {
        height: 25px !important;
        }
    }
    .form-group {
        margin-bottom:15px;
        position: relative;
/*         &:last-child {
            margin-bottom: 0;
        } */
        &.has-form-group-icon, .has-form-group-icon {
        position: relative;
            .form-control {
                padding-right: 35px;
            }
            .form-group-icon {
        position: absolute;
        right: 0;
        bottom: 4px;
        font-size: 0.7rem;
        background-repeat: no-repeat;
        background-position: center center;
        color: #fff;
        &.fa {
        font-size: 1rem;
        }
        &.send-otp-icon, &.resend-otp-icon {
            background-image: url(../images/svgs/send-otp-icon.svg);
            width: 22px;
            height: 28px;;
            background-size: 100%;
        }
        &.resend-otp-icon {
            background-image: url(../images/svgs/resend-otp-icon.svg);
            width: 20px;
            height: 28px;
        }
    }
        }
        .info-icon {
            right: -15px;
        }
    }
    
    textarea.form-control {
        resize: none;
        border-bottom: 1px solid $form-border-color !important;
        min-height: 50px;
        margin-top: 10px;
        padding: 10px;
    }

    .form-note {
        font-size: 0.625rem;
        margin-top: 6px;
        display: block;
        color: $theme-color;
    }

    .intl-tel-input {
    display: block;
    }
    .form-group-captcha > div {
    text-align: center;
    }

    .calendar-icon {
      position: absolute;
    right: 0;
    color: #040404;
    bottom: 10px;
    font-size: 17px;
    cursor: pointer;
    }
}

.otp-link {
    @include media-breakpoint-down(sm) {
        font-size: 14px !important;
    }
}

.form-heading-box {
    border: 1px solid $form-border-color;
    padding: 5px 15px;

}

.centered-form {
    display: table;
    margin-right: auto;
    margin-left: auto;
    width: 450px;
    @include media-breakpoint-down(sm) {
        width: 100%;
    }
}

.small-form {
    .form-control, .has-datepicker {
        max-width: 20.375rem;
    }
    
}

.subscribe-form {
    position: relative;
    max-width: 33.125rem;
    margin: 0 auto;
    .form-control,
    .submit-btn {
        height: 3.75rem;
        @include border-radius(2.5rem);

    }
    .submit-btn {
        cursor: pointer;
        color: #fff;
        font-size: 0.75rem;
        max-width: 11.25rem;
        @include media-breakpoint-down(sm) {
            max-width: 100%;
            width: 100%;
        }
    }
    .form-control,
    .form-group-input {
        width: 100%;
    }
    .form-control {
        color: $theme-color !important;
        padding: 0px 11.875rem 0px 1.5625rem;
        border: 1px solid $theme-color !important;
        @include media-breakpoint-down(xs) {
            padding: 0 15px;
        }
    }
    .form-group-submit {
        position: absolute;
        right: 0;
        top: 0;
        @include media-breakpoint-down(xs) {
            position: static;
            width: 100%;
        }
    }
}


.select-currency-form {
    > .form-group {
        position: relative;
        height: 50px;
        margin-bottom: 20px;
        display: flex;
        border: 1px solid $form-border-color;
    }
    .form-control {
        height: 100%;
        padding-right: 120px;
        padding-left: 15px;
        background-color: #fff;
        border: none !important;
        outline: none;
        @include transition(150ms);
        @include border-radius(0);
    }
    .form-dropdown {
        right: 0;
        @include vertically-centered();
    }

    .form-control-box {
        border: 1px solid #ddd;
        border-top: none;
        color: $theme-color;
        padding: 20px 10px;

        p.bold-font {
            font-size: 1rem;
            margin: 0;
        }
    }
    .form-control-box-list {
        font-weight: 600;
        font-size: 0.75rem;
        > li {
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .form-dropdown-icon {
        margin-left: 10px;
    }
}

.uploaded-photo-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.text-section{
	*{color:$theme-color!important;}
	*.btn{color:#fff!important;}
	*.asterik{
		color:#ff0000!important
	}
}

.req-password{
    display: table;
    width: 100%;
    margin: 10px 0px;
}
.req-password{
   li{
		float: left;
		width: auto;
		font-size: 12px;
		/* font-weight: 600; */
		position: relative;
		padding-left: 8px;
		color:#000;
		margin: 4px 10px 4px 0px;
	&::before{
			content: '';
			display: inline-block;
			width: 5px;
			height: 5px;
			-moz-border-radius: 7.5px;
			-webkit-border-radius: 7.5px;
			border-radius: 7.5px;
			background-color: #69b6d5;
			position: absolute;
			left: -3px;
			top: 5px;
			box-shadow: 0px 0px 5px #69b6d5;
	}
	
  }
}
.custom-select:disabled{
	color:#fff;
}